
  export default {
    middleware: 'anonymous',
    data(){
      return{
        valid: false,
        email: '',
        password: '',
        passwordCheck: '',
        nickname: '',
        terms: false,
        emailRules: [
          v => !!v || '이메일은 필수입니다.',
          v => /.+@.+/.test(v) || '이메일이 유효하지 않습니다',
         ],
         passwordRules: [
          v => !!v || '비밀번호는 필수입니다.'
         ],
         passwordCheckRules: [
          v => !!v || '비밀번호 확인은 필수입니다.',
          v => v === this.password || '비밀번호가 일치하지 않습니다.',
         ],
         nicknameRules: [
          v => !!v || '닉네임은 필수입니다.',
         ]
      }
    },
    head(){
      return{
        title: '회원가입'
      }
    },
    computed:{
      me(){
        return this.$store.state.user.me;
      }
    },
    watch: {
      me(value){
        if(value){
          this.$router.push({
            path:'/',
          });
        }
      }
    },
    methods:{
      async onSubmitForm(){
        if(this.$refs.form.validate()){
          try{
            const signUpRes = await this.$store.dispatch('user/signUp', {
              nickname: this.nickname,
              email: this.email,
              password: this.password,
            });

            if(!signUpRes.result){
              alert(signUpRes.msg);
              return;
            }

            this.$router.push({
              path: '/',
            });
            
          }catch(err){
            alert('회원가입 실패 에러');
          }
        }
      }
    }
  }
